/* Add padding BETWEEN each column */
.row,
.row > .column {
  padding: .5em;
  margin-right: auto;
  margin-left: auto;
}

/* Create four equal columns that floats next to each other */
.column {
  margin: 0 auto;
	float: left;
	width: 30%;
  display: flex;
  padding: auto 8px;
  display: block;
}

.background {
  position: relative;
}

.background::before {
  margin: 1.25em 4em 1.25em 3%;
  width: 97%;
  content: "";
  position: absolute;
  overflow: hidden;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  box-sizing: border-box;
}

.background.side-project::before {
  background: transparent url("/images/dots-side-project.svg") repeat bottom right;
}

.background.wsj::before {
  background: transparent url("/images/dots-wsj.svg") repeat bottom right;
}

.background.nbc::before {
  background: transparent url("/images/dots-nbc.svg") repeat bottom right;
}

.background.words::before {
  background: transparent url("/images/dots-words.svg") repeat bottom right;
}

.background.transit::before {
  background: transparent url("/images/dots-transit.svg") repeat bottom right;
}

.content {
  width: 100%;
	/*padding: .75em 1em;*/
  margin: 1em 0em 1em 0em;
  display: inline-block;
  overflow: hidden;
  position: relative;
  vertical-align: top;
  z-index: 2;
  box-sizing: border-box;
  background-color: #f8f8f8;
  cursor: pointer;
  transition: transform .2s linear;
}

.content > img {
  -webkit-filter: grayscale(60%);
    filter: grayscale(60%);
    transition: all .2;
    margin-top: .5rem;
}

.content:hover > img {
  -webkit-filter: none;
    filter: none;
    transition: all .2s;
}

.content:hover {
  transform: translate(-12px, -12px);
}

