}
.arrow {
  position: absolute;
  bottom: 0%;
  right: 5%;
  margin-left: -20px;
  width: 40px;
  height: 40px; /*change with size of arrow to make sit on bottom */
/*   background-image: url(); */
/*   background-size: contain; */
}

.bounce {
  -moz-animation: bounce 2s infinite;
  -webkit-animation: bounce 2s infinite;
  animation: bounce 2s infinite;
}
  .noselect {
    -webkit-touch-callout: none;
      -webkit-user-select: none;
       -khtml-user-select: none;
         -moz-user-select: none;
          -ms-user-select: none;
              user-select: none;
  }

 .doodle {
	position: absolute;
  z-index: -10;
  margin: 0 auto;
  overflow: hidden;
} 

/* Mobile */
@media (max-width:700px) {
  .column {
    width: 100%;
    margin: 8px auto;
  }
  .content {
    margin: auto;
  }
  .background::before {
    margin: 0.25em 0em 0% 3%;
  }
}

@media (min-width:700px) and (max-width:925px) {
  .column {
    width: 50%;
    margin: 8px auto;
  }
}

@media (max-width: 800px) {
  .headline {
    font-size: 5rem;
  }
  .currently {
    font-size: 1.65rem;
  }
  .header-container {
    bottom: 20%;
  }
  .header {
    max-width: 100%;
  }
}

@media (max-width: 350px) {
  .header-container {
    bottom: 0%;
  }
}

@media (min-width: 800px) and (max-width: 1200px) {
    .headline {
    font-size: 10vw;
  }
  .currently {
    font-size: 3.2vw;
  }
}

@media (min-width: 1200px) {
  .headline {
    font-size: 8vw;
  }
  .currently {
    font-size: 2.5vw;
  }
}

